a.symbol:before {
  content: attr(title);
  margin-right: 0.3em;
  font-size: 130%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.symbol {
  font-size: 40px;
  display: inline;
}
.social {
  margin-top: 10px;
}
.plinks {
  text-decoration: none;
  color: inherit;
  padding: 05px;
}
.facebook:hover {
  color: #3b5998;
}
.twitter:hover {
  color: #4099ff;
}

.instagram:hover {
  color: #fbad50;
}
.linkedin:hover {
  color: #0e76a8;
}
.github:hover {
  color: #00c853;
}

.mdl-mini-footer {
  display: block;
}

.view {
  margin-top: 56px;
  margin-bottom: 110px;
  margin-left: 0px;
  margin-right: 0px;
}
.mainimage {
  height: 200px;
  width: 200px;
  border-radius: 200px;
  margin-top: 10%;
}
.quote {
  margin-right: 10px;
  margin-left: 10px;
}
.img-responsive,
.width100 {
  width: 100%;
}

.sidemenu:hover {
  text-decoration: none;
}

.sidemenu {
  font-size: 15px;
}

.contact {
  margin-bottom: 10px;
}

.contact-top-bottom {
  margin-top: 20px;
  margin-bottom: -65px;
}

.education-top-bottom {
  margin-top: 30px;
}

@media (min-width: 768px) {
  h2 {
    font-size: 35px;
  }
  h3 {
    font-size: 25px;
  }
  h4 {
    font-size: 15px;
  }
  .view {
    min-height: calc(100%-56px);
  }
  .header-icon {
    font-size: 16px;
    padding-left: 5px;
  }
  .mainimage {
    height: 300px;
    width: 300px;
    border-radius: 300px;
    margin-top: 0.5%;
  }
  .quote {
    max-width: 500px;
  }
  .grid {
    padding: 0 25px;
  }
  .grid-large-box {
    margin-bottom: 25px;
  }
  .grid-large-box-details {
    padding: 5px 0 5px 20px;
  }
  .grid-small-box {
    width: 30.33%;
    padding: 0 3% 3% 0;
  }
  .grid-small-box-test {
    width: 4.33%;
    padding: 0 3% 3% 0;
  }
}
